body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* General Style Applications  */

body {
  width: 100%;
  margin: 0;
  font-family: sans-serif, arial;
}


/* Oversizing catch-all */

img,
embed,
object,
video {
  max-width: 100%;
}


/* Remove decoration from links */

a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: inherit;
}


/* Theme Variables */

:root {
  --theme-color-p: #6d6d6d;
  --theme-color-s: #424242;
  --theme-color-h: #90caf9;
  --theme-elem-bg-trans: #ffffff70;
  --theme-elem-bg-trans-d: #00000080;
  --theme-elem-bg-on-scroll: #fff;
  --theme-box-shadow: 2px 3px 10px 1px var(--theme-color-s);
  --theme-font-p: 'Spectral', sans-serif;
  --theme-font-s: 'Raleway';
  --theme-trans-bg: url(data:image/png;base64,);
  --theme-trans-bg2: url(data:image/png;base64,);
}


/* Settings on React Root Element */

#root {
  position: absolute;
  width: 100%;
  height: 100%;
}


/* Setup Grid on top level div */

#top-level-container {
  display: grid;
  grid-template-columns: [col-start] 100% [col-end];
  grid-template-rows: [line-one] 350px [line-two] 60px [line-three] 1fr [line-four] 50px [line-five];
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
}


/* Re-used styles */

p {
  text-indent: 1em;
}

.main-font-style {
  font-family: 'Spectral', sans-serif;
  font-family: var(--theme-font-p);
  color: #fff;
}

.flex-c-nw {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.flex-row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.hide {
  display: none;
}

.prevent-click {
  pointer-events: none;
}

.small-note {
  margin: 0;
  padding: 0;
  font-family: 'Spectral', sans-serif;
  font-family: var(--theme-font-p);
  font-size: 0.7em;
}

.dark-bg {
  background-color: #424242;
  background-color: var(--theme-color-s);
  background-image: url(data:image/png;base64,), radial-gradient(farthest-side, #6d6d6d 5%, #424242);
  background-image: var(--theme-trans-bg2), radial-gradient(farthest-side, var(--theme-color-p) 5%, var(--theme-color-s));
}

.top-spacer {
  padding-top: 3em;
}



